<template>
<div>
    <!-- <div class="header">
      <mt-header fixed title=""></mt-header>
    </div> -->
    <mt-tabbar v-model="selected">
  <mt-tab-item id="">
    <img slot="icon" src="./100x100.png">
    
  </mt-tab-item>
  <mt-tab-item id="">
    <img slot="icon" src="./100x100.png">
    
  </mt-tab-item>
  <mt-tab-item id="">
    <img slot="icon" src="./100x100.png">
    
  </mt-tab-item>
  <mt-tab-item id="">
    <img slot="icon" src="./100x100.png">
    
  </mt-tab-item>
</mt-tabbar>
    
    <div>
    <mt-search
      v-model="searchValue"
      cancel-text=""
      placeholder=""
      :show="true">
      <mt-cell
        v-for="item in searchResult"
        :title="item.title"
        :value="item.value">
      </mt-cell>
    </mt-search>
    </div>
    <mt-switch v-model="switchValue"></mt-switch>
    <div>
      <mt-checklist
        title=""
        v-model="checkListValue"
        :options="options">
      </mt-checklist>
    </div>
    <mt-radio
      title=""
      v-model="radioValue"
      :options="options">
    </mt-radio>
    <mt-cell-swipe
      title="2"
      :right="[
        {
          content: 'Delete',
          style: { background: 'red', color: '#fff' },
          handler: () => this.$messagebox('delete')
        }
      ]">
    </mt-cell-swipe>
    <mt-spinner type="snake" color="#26a2ff" :size="60">snake</mt-spinner>
    <mt-spinner type="double-bounce" color="#26a2ff">double-bounce</mt-spinner>
    <mt-spinner type="triple-bounce" color="#26a2ff">triple-bounce</mt-spinner>

    <mt-cell title=""> <mt-spinner type="fading-circle" color="#26a2ff">fading-circles</mt-spinner></mt-cell>
    <mt-cell title="" value="">
        <mt-badge size="small" type="error">30
        </mt-badge>
    </mt-cell>

    <mt-field 
      label="" 
      placeholder="" 
      type="textarea" 
      rows="4" 
      v-model="introduction">
      
    </mt-field>

    <mt-navbar v-model="selected">
      <mt-tab-item id="1"></mt-tab-item>
      <mt-tab-item id="2"></mt-tab-item>
      <mt-tab-item id="3"></mt-tab-item>
    </mt-navbar>
    <div class="tab-container">
    <!-- tab-container -->
    <mt-tab-container v-model="selected" :swipeable="true">
      <mt-tab-container-item id="1">
        <mt-cell v-for="n in 10" :title="' ' + n" />
      </mt-tab-container-item>
      <mt-tab-container-item id="2">
        <mt-cell v-for="n in 4" :title="' ' + n" />
      </mt-tab-container-item>
      <mt-tab-container-item id="3">
        <mt-cell v-for="n in 6" :title="' ' + n" />
      </mt-tab-container-item>
    </mt-tab-container>
    </div>

    <!-- <mt-tabbar v-model="tabbar" :fixed="true">
      <mt-tab-item id="">
        <img slot="icon" src="./index/img/types/types (7).png">
        
      </mt-tab-item>
      <mt-tab-item id="">
        <img slot="icon" src="./index/img/types/types (7).png">
        
      </mt-tab-item>
      <mt-tab-item id="">
        <img slot="icon" src="./index/img/types/types (7).png">
        
      </mt-tab-item>
      <mt-tab-item id="">
        <img slot="icon" src="./index/img/types/types (7).png">
        
      </mt-tab-item>
    </mt-tabbar>
    <mt-tab-container v-model="tabbar" :swipeable="true">
      <mt-tab-container-item id="">
        <mt-cell v-for="n in 10" :title="' ' + n" />
      </mt-tab-container-item>
      <mt-tab-container-item id="">
        <mt-cell v-for="n in 4" :title="' ' + n" />
      </mt-tab-container-item>
      <mt-tab-container-item id="">
        <mt-cell v-for="n in 6" :title="' ' + n" />
      </mt-tab-container-item>
       <mt-tab-container-item id="">
        <mt-cell v-for="n in 6" :title="' ' + n" />
      </mt-tab-container-item>
    </mt-tab-container> -->
    <mt-actionsheet
      :actions="actions"
      v-model="sheetVisible">
    </mt-actionsheet>

    <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" 
    :bottom-all-loaded="allLoaded" ref="loadmore">
      <ul>
        <li v-for="item in list">{{ item }}</li>
      </ul>
    </mt-loadmore>

    <ul>
      <li v-for="item in listItem">
        <img v-lazy="item">
      </li>
    </ul>
    <mt-progress :value="progressValue">
      <div slot="start">0%</div>
      <div slot="end">100%</div>
    </mt-progress>

    <mt-range
      v-model="rangeValue"
      :min="0"
      :max="100"
      :step="10"
      :bar-height="5"
      @change="rangeChange">
      <div slot="start">0</div>
      <div slot="end">100</div>
    </mt-range>
    <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
    <mt-datetime-picker
      v-model="pickerVisible"
      type="date"
      ref="picker"
      year-format="{value} "
      month-format="{value} "
      date-format="{value} "
      @confirm="handleConfirm">
    </mt-datetime-picker>
        <mt-index-list>
      <mt-index-section index="A">
        <mt-cell title="Aaron"></mt-cell>
        <mt-cell title="Alden"></mt-cell>
        <mt-cell title="Austin"></mt-cell>
      </mt-index-section>
      <mt-index-section index="B">
        <mt-cell title="Baldwin"></mt-cell>
        <mt-cell title="Braden"></mt-cell>
      </mt-index-section>
      <mt-index-section index="Z">
        <mt-cell title="Zack"></mt-cell>
        <mt-cell title="Zane"></mt-cell>
      </mt-index-section>
    </mt-index-list>
    <div>


   
    <mt-palette-button content="+" @expand="main_log('expand')" @expanded="main_log('expanded')" @collapse="main_log('collapse')"
      direction="rt" class="pb" :radius="80" ref="target_1" mainButtonStyle="color:#fff;background-color:#26a2ff;"
      style="left:30px;">
      <div class="my-icon-button indexicon icon-popup" @touchstart="sub_log(1)"></div>
      <div class="my-icon-button indexicon icon-popup" @touchstart="sub_log(2)"></div>
      <div class="my-icon-button indexicon icon-popup" @touchstart="sub_log(3)"></div>
    </mt-palette-button>
    </div>

    <button @click='popup'></button>
    <button @click='active'> action sheet</button>
    <button @click='toast'>toast</button>
    <button @click='indicator'>indicator</button>
    <button @click='openPicker'></button>
    <mt-button size="small">small</mt-button>
    <mt-button size="large">large</mt-button>
    <mt-button size="normal">normal</mt-button>
    <mt-button type="default">default</mt-button>
    <mt-button type="primary">primary</mt-button>
    <mt-button type="danger">danger</mt-button>



   
</div>
</template>
<script>
import { MessageBox,Toast,Indicator   } from 'mint-ui';

export default {
    name: 'HelloWorld',
    data () {
        return {
            searchValue:"",
            msg: 'Welcome to Your Vue.js App',
            list:[
              '',
              ''
            ],
            allLoaded:false,
            actions:[
            {
                name:'1',
                method: this.actionsheet//
            },
          ],
          sheetVisible:false,
          cancelText:'', //

          rangeValue:70,
          listItem:[
            require('./index/img/types/types (7).png')
          ],
          progressValue:90,

          slots: [
              {
                flex: 1,
                values: ['2015-01', '2015-02', '2015-03', '2015-04', '2015-05', '2015-06'],
                className: 'slot1',
                textAlign: 'right'
              }, {
                divider: true,
                content: '-',
                className: 'slot2'
              }, {
                flex: 1,
                values: ['2015-01', '2015-02', '2015-03', '2015-04', '2015-05', '2015-06'],
                className: 'slot3',
                textAlign: 'left'
              }
            ],
          pickerVisible:"", 
          selected:"2",//id
          tabbar:"",//id
          searchResult:[
            {
              title:'1',
              value:'1'
            },
            {
              title:'2',
              value:'2'
            },
            {
              title:'3',
              value:'3'
            }
          ],
          options:[
            {
              label: '',
              value: 'F',
              disabled: true
            },
            {
              label: '',
              value: '',
              disabled: true
            },
            {
              label: 'A',
              value: 'A'
            },
            {
              label: 'B',
              value: 'B'
            }
          ],
          checkListValue:[],
          radioValue:"",
          switchValue:"",
          introduction:"",

        }
                 
    },

    methods: {

      popup(){
        console.log('dsd');
        MessageBox.confirm('?').then(action => {
          console.log(action);
        });
      },
      loadTop() {
        // 
        this.list.push("1");
        this.list.push("2");
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom() {
        // 
        this.list.push('4');
        this.allLoaded = true;// 
        this.$refs.loadmore.onBottomLoaded();
      },
    


      actionsheet(){
         console.log('');
      }, 
      active(){
        this.sheetVisible=true
      },
      toast(){
        Toast({
          message: '',
          position: 'bottom',
          duration: 5000,
          //iconClass: 'icon icon-success'  //
        });
      },
      indicator(){
        Indicator.open({
          text: '...',
          spinnerType: 'snake'//'snake''fading-circle''double-bounce''triple-bounce'
        });
        
        setTimeout(function () {
            Indicator.close();
          }, 1000);
      },
      rangeChange(){
        console.log(this.rangeValue);
      },
      onValuesChange(picker, values) {
        if (values[0] > values[1]) {
          console.log(values);
          picker.setSlotValue(1, values[0]);
        }
      },
      openPicker() {
        this.$refs.picker.open();
      },
      handleConfirm(value){
        console.log(value);
        console.log(this.pickerVisible);
      },
       main_log(val) {
        console.log('main_log', val);
      },
      sub_log(val) {
        console.log('sub_log', val);
        this.$refs.target_1.collapse();
      },
      

  }
}
</script>
<style scoped>
  .header{
    width: 100%;
    height: 50px;
  }
</style>


